@media screen and (min-width: 960px) { 
  header {
    display: flex;
    justify-content: space-between;
    z-index: 100;
  
    width: 100%;
    height: 70px;
    border-bottom: solid 2.5px #00A8CC;
    background-color: rgba(255, 255, 255, 0.87);
    box-shadow: 1px 1px 1px 1px rgba(0,0,0,0.15);
    position: fixed;
  
    transition: all 0.3s;
  }

  .header-right{
    display: flex;  
    text-decoration: none;
  }
  
  h2{
    margin: 0;
  }

}

@media screen and (max-width: 960px) { 
  header {
    display: flex;
    flex-direction: column;
    height: 150px;
  }

  .header-left{

  }

  .header-right{
    width: 100%;
    display: flex;  
    text-decoration: none;
    justify-content: space-around;
  }
  
  h2{
    margin: 0;
  }
  
}
@media (min-width: 960px) {
  .top h1 {
    color: rgb(230, 230, 230);
    font-size: 60px;
    padding-top: 100px;
    text-align: center;
  }

  .top {
    height: 600px;
    background-color: #474949;
    background-image: url(/KamikamiLab/static/media/Home.cbe62019.png);
    background-size: cover;
    position: relative;
  }

  .home {
    padding-top: 70px;
  }

  .content {
    margin: 0 auto;
    margin-top: 180px;
    margin-bottom: 300px;
    width: 600%;
    max-width: 950px;
  }

  .content-skill {
    margin: 0 auto;
    margin-bottom: 300px;
    text-align: center;
    height: 55px;
    width: 100%;
  }

  .content-skill h3 {
    font-size: 40px;
  }

  .content-skill-list {
    margin-top: 0px;
    margin-bottom: 80px;
    display: flex;
    justify-content: space-between;

    /* animation-name: fadein; */
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
  }

  .skill-lang {
    width: 48%;
  }

  .skill-lang-content {
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    border: solid 1px #00a8cc;
    border-radius: 1%;
    background-color: white;
  }

  .skill-lang-content h2 {
    margin: 30px;
  }

  .skill-framework {
    width: 48%;
  }

  .skill-framework-title {
    width: 50%;
    margin: 0 auto;
  }

  .skill-framework-content {
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    border: solid 1px #00a8cc;
    border-radius: 1%;
    background-color: white;
  }

  .skill-framework-content h2 {
    margin: 30px;
  }

  /* recent-work */
  .recent-work {
    margin: 0 auto;
    width: 65%;
  }

  .recent-work h3 {
    font-size: 40px;
  }

  .recent-work h2 {
    margin-top: 30px;
    text-align: center;
  }

  .recent-work-content {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }

  .recent-work-content-left {
    background-color: #474949;
    width: 45%;
    background-size: cover;
    height: 300px;
  }

  .recent-work-content-right {
    width: 45%;
    margin-left: 10px;
  }

  .recent-work-content-left img {
    width: 50%;
    height: 100%;
  }

  .recent-work-content-right {
  }

  /* recent-work-end */

  /* contact */
  .contact h3 {
    font-size: 40px;
  }

  .contact {
    margin: 60px auto;
    max-width: 400px;
  }

  .contact h2,
  h3 {
    text-align: center;
  }

  .contact-icon {
    display: flex;
    justify-content: center;
  }

  .contact-content {
    margin-top: 20px;
  }

  /* contact-end */

  @-webkit-keyframes fadein {
    from {
      opacity: 0;
      transform: translateY(150px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  @keyframes fadein {
    from {
      opacity: 0;
      transform: translateY(150px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
}

